/* 基本布局 */
.container {
  text-align: center;
  padding: 20px;
}

/* 输入框 & 选择框 */
input, select {
  padding: 8px;
  margin: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

/* 按钮 */
button {
  padding: 8px 15px;
  margin: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

/* 表格样式 */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th, td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: center;
}

/* 表头排序 */
th {
  background-color: #007bff;
  color: white;
  cursor: pointer;
  position: relative;
  user-select: none;
}

th:hover {
  background-color: #0056b3;
}

/* 排序箭头 */
th::after {
  content: ' ';
  display: inline-block;
  margin-left: 8px;
}

th[aria-sort="asc"]::after {
  content: '▲';
}

th[aria-sort="desc"]::after {
  content: '▼';
}

/* Commutator 列输入框 */
th input {
  width: 100px;
  padding: 5px;
  font-size: 14px;
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 3px;
}

th input:focus {
  border: 2px solid #007bff;
  outline: none;
}

/* 悬浮提示框 */
.tooltip {
  position: fixed;
  background: rgba(0, 0, 0, 0.85);
  color: white;
  padding: 6px 12px;
  border-radius: 5px;
  font-size: 14px;
  white-space: nowrap;
  pointer-events: none;
  transform: translate(10px, -10px);
  z-index: 1000;
}

/* 让 tooltip 平滑出现 */
.tooltip::before {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%);
  border-width: 5px;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.85) transparent transparent transparent;
}

/* 隐藏公式时，表格调整 */
.hidden-column {
  display: none;
}
